                            content: "온라인 설문조사는 구글 설문지를 선택 하여 진행 하였다. 주변 배달의 민족을 자주 사용하는 지인들을 대상으로 진행 했으며 참여자는 49명이다. 첫 번째 문항은 “배달의 민족을 실행 후 첫 메인화면을 봤을 때 전체적인 느낌 혹은 개선이 필요한 점“이다. 이 문항을 도출한 이유는 현재의 배달의 민족 앱의 메인 화면에서는 한 눈에 검색창이 바로 눈에 들어오지 않고 밑으로 스크롤 해야 나오는데, 처음 사용하는 사용자의 경우에는 찾기가 어려울 수도 있다. 검색창은 원하는 것을 쉽게 찾도록 하는 기능인데 이 검색창을 찾기가 어려우면 안된다고 생각 되었다. 제작자 입장에서는 앱을 스크롤 하여 전체적으로 한번 보라는 느낌으로 검색창을 밑에 두었을 수도 있겠지만, 사용자가 찾기가 어렵다면 좋은 디자인이라고 볼 수 없기 때문이고 이 외에도 개선 필요성이나 추가 사항을 파악하기 위하여 이 질문을 도출했다. 설문 결과는 현재의 인터페이스에 만족하며 사용한다는 응답자는 전체의 59.5%이며, 개선이 필요하다고한 응답자는 40.3%이다. 이 중 대다수의 응답자들이 ”검색창“이 바로 보이지 않는 다는 점을 꼽았으며 그 뒤로는 전체적으로 좀 더 세부적이면 좋겠다라는 의견도 있었다. 두 번째 문항은 ”음식점 리스트 화면에서의 전체적인 느낌 혹은 개선이 필요한 점”이다. 이 문항을 도출한 이유는 메인화면에서 음식 카테고리를 터치하면 나오는 해당 카테고리 음식점 리스트 화면에서는 검색창이 존재하지 않는데 이 때문에 검색을 위해서는 다시 메인화면으로 돌아가 검색을 해야 해서 사용자에게 추가적인 과정을 요구하는 불편함으로 판단하였고 이 외에도 개선 필요성이나 추가 사항을 파악하기 위하여 이 질문을 도출했다. 설문 결과는 현재의 인터페이스에 만족하며 사용한다는 응답자는 전체의 54.2%이며, 개선이 필요하다고한 응답자는 45.8%이다. 이 중 다수의 개선 필요 사항으로 “검색창”의 부재를 꼽았으며 그 뒤로는 메뉴배치의 통일성, 한 눈에 들어오는 음식점리스트, 사진 크기 및 가격 구간의 의견도 있었다. 세 번째 문항은 “현재의 리뷰 페이지는 충분하다고 생각 하나요?” 이다. 이 문항을 도출한 이유는 다른 조사에서 소비자의 90%이상[6]이 리뷰를 중요시 여긴다 라는 조사 결과가 있어서 도출했다. 설문 결과는 현재의 인터페이스에 만족하며 사용 한다는 응답자는 전체의 64.6%이며, 개선이 필요하다고한 응답자는 35.4%이다. 이 중 각각의 음식만 나오는 리뷰 화면이 있었으면 좋겠다 라는 의견이 있었으며, 나머지 응답자들은 개선이 필요하다고만 응답하였다. 네 번째 문항은 “현재 배달의 민족 앱에서 제공하는 카테고리 선택 항목에 대해 개선 점이나 추가 됐으면 하는 것이 있나요?” 이다. 이 문항을 도출한 이유는 기존에는 음식 카테고리만 존재하는데, 사용자의 현재 상황에 맞는 카테고리 들이 추가 되는 등 카테고리 항목의 다양성을 위하여 도출했다. 설문 결과로는 현재의 인터페이스에 만족하며 사용한다는 응답자는 전체의 62.5%이며, 개선이 필요하다고한 응답자는 37.6%이다. 이 중 다수의 응답자는 새로운 카테고리의 추가이고 그 뒤로는 카테고리의 정확성의 의견이 있었다. 다섯 번째 문항은 “두 군데 이상의 음식점 비교 시 어려운 점은 없나요?”이다. 이 문항을 도출한 현재의 배달의 민족은 복수 음식점 비교 기능이 없어서 화면을 여러 번 변경해야 하는데 이에 대해서 불편한 점이 없는지 혹은 기타 의견이 존재하는지를 확인하기 위하여 도출했다. 설문 결과로는 현재의 인터페이스에 만족하며 사용한다는 응답자는 전체의 47.9%이며, 개선이 필요하다고 한 응답자는 52.5%이다. 이 중 대다수의 응답자들은 두 군데를 한 번에 비교 할 수 있도록 혹은 한 눈에 비교가 어려움 등 두 가지 업체를 화면을 여러 번 넘길 필요 없이 한 눈에 비교 할 수 있도록 개선됐으면 좋겠다라는 의견이였다.  마지막 문항은 “업체 선택 후 음식을 찾을 때 어려운 점은 없나요?” 이다. 이 문항을 도출한 이유는 같은 배달의 민족 앱 이라도 업체 마다 메뉴 선택이나 인터페이스 구성이 완전 다르기 때문에 선택에 어려움이 있을 수 있어서 이에 대한 의견을 들어보고자 도출 하였다. 현재의 인터페이스에 만족한다는 사용자는 전체의 52.1%이며 개선이 필요하다고한 응답자는 48.1%이다. 이 중 다수의 응답자가 음식사진의 부재이고 그 뒤로는 중복 업체 다수 존재의 개선사항을 서술 하였는데 이는 우리가 할 수 있는 개선사항이 아니고 해당 업체에서 개선해야 하는 문제로 판단하여 이 후 와이어프레임과 프로토타입에서는 제외하였다. 이 때문에 초기에는 총6개 문항 이였지만 마지막 문항을 제외하여 5개의 문항으로 와이어프레임 및 프로토타입을 진행했다.",
